import React, { Component } from "react"
import { Row, Col, Table, Button } from "react-bootstrap"
import { connect } from "react-redux"
import { FaChevronLeft, FaChevronRight } from "react-icons/fa"
import { getKits, removeKit, addKit, getHardwares, sendCalibrationNotification, updateKit } from "@actions/inventory"
import { selectRoleAuthorization } from "@selectors/authorization"
import { selectKits, kitErrors, selectHardwares, loadingHardwares } from "@selectors/inventory"
import { KitForm } from "@components/Dashboard/Inventory/Kit/KitForm"
import { EditKitForm } from "@components/Dashboard/Inventory/Kit/EditKitForm"
import InventoryIssues from "@components/Dashboard/Inventory/Issues"
import { ModalDelete } from "@components/shared/ModalDelete"
import { Loading } from "@components/shared/Loading"
import { Dashboard } from "@components/Dashboard"
import { issueCategory } from "@data"
import moment from "moment"
import PropTypes from "prop-types"

class Kit extends Component {
  state = {
    isModalOpen: null,
    currentDate: new Date(),
    inventoryData: null,
    kitId: null,
    currentPage: 1,
  }

  componentDidMount() {
    this.props.getKits()
    this.props.getHardwares()
  }

  toggleModal = isModalOpen => {
    this.setState({ isModalOpen })
  }

  addViewIssue = (id, name) => {
    this.setState({
      inventoryData: {
        id,
        name,
      },
    })
  }

  editKit = id => {
    const { kits } = this.props
    this.setState({ kitId: kits.find(kit => kit.id === id) })
  }

  pageSwitcher = value => {
    if (value === "next") {
      this.setState({ currentPage: this.state.currentPage + 1 })
    } else {
      this.setState({ currentPage: this.state.currentPage - 1 })
    }
  }

  render() {
    const { roleName, error, kits, hardwares, addKit, updateKit, loading, removeKit } = this.props
    const { isModalOpen, currentDate, kitId, inventoryData, currentPage } = this.state

    let alertName = ""
    let difference = ""

    const lastItem = currentPage * 10
    const firstItem = lastItem - 10

    let formatedItems = []
    formatedItems =
      kits.length && kits.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt)).slice(firstItem, lastItem)

    const renderPageNumbers = () => {
      return (
        <Row className="pagination">
          <Col lg="9">
            <p>
              Showing {firstItem + 1}-{kits.length > firstItem && kits.length < lastItem ? kits.length : lastItem} of{" "}
              {kits && kits.length} entries
            </p>
          </Col>
          <Col>
            <div className="arrows d-flex justify-content-end">
              <Button onClick={() => this.pageSwitcher("prev")} disabled={firstItem === 0}>
                <FaChevronLeft />
              </Button>
              <Button onClick={() => this.pageSwitcher("next")} disabled={lastItem >= kits.length}>
                <FaChevronRight />
              </Button>
            </div>
          </Col>
        </Row>
      )
    }

    if (loading) return <Loading />
    if (!formatedItems.length) return null

    return (
      <Dashboard>
        <div className="ms-panel">
          <div className="ms-panel-header">
            <h6>Hardware Kits</h6>
          </div>
          <div className="ms-panel-body">
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Hardware</th>
                  <th>Serial Number</th>
                  <th>Calibration Number</th>
                  <th>Current Cal Date</th>
                  <th>Current Due Date</th>
                  <th>Issues</th>
                  {(roleName === "admin" || roleName === "technical-manager") && (
                    <>
                      <th>Edit</th>
                      <th>Delete?</th>
                    </>
                  )}
                </tr>
              </thead>
              <tbody>
                {formatedItems.map(
                  ({ id, name, isFaulty, hardwares, calibrationNumber, calibrationCurDate, calibrationDueDate }) => (
                    <tr key={id} className={`${isFaulty && "notavailable"}`}>
                      <td>{name}</td>
                      <td>
                        {hardwares.map(({ type, model }) => (
                          <p>
                            {type.name} {model.manufacture.name} {model.name}
                          </p>
                        ))}
                      </td>
                      <td>
                        {hardwares.map(({ serialNumber }) => (
                          <p> {serialNumber}</p>
                        ))}
                      </td>
                      <td>{calibrationNumber}</td>
                      <td>{calibrationCurDate.substr(0, 10)}</td>
                      <td>
                        {
                          ((difference = moment.duration(new Date(calibrationDueDate) - currentDate, "milliseconds")),
                          (alertName =
                            currentDate > new Date(calibrationDueDate)
                              ? "redAlert"
                              : difference.asDays() < 14 && difference.asDays() > 0
                              ? "yellowAlert"
                              : null),
                          (<a className={alertName}>{calibrationDueDate.substr(0, 10)}</a>))
                        }
                      </td>
                      <td>
                        <Button id={id} variant="primary" onClick={() => this.addViewIssue(id, name)}>
                          Add/View
                        </Button>
                      </td>
                      {(roleName === "admin" || roleName === "technical-manager") && (
                        <>
                          <td>
                            {
                              <Button id={id} variant="primary" onClick={() => this.editKit(id)}>
                                Edit/Add Item
                              </Button>
                            }
                          </td>
                          <td>
                            <Button id={id} variant="primary" onClick={() => this.toggleModal(id)}>
                              Delete Item
                            </Button>
                            <ModalDelete
                              id={id}
                              hideModal={this.toggleModal}
                              isModalOpen={isModalOpen}
                              deleteState={() => removeKit(id)}
                            />
                          </td>
                        </>
                      )}
                    </tr>
                  )
                )}
              </tbody>
            </Table>
            {renderPageNumbers()}
          </div>
        </div>
        <Row>
          {(roleName === "admin" || roleName === "technical-manager") && (
            <Col lg="5">
              <div className="ms-panel">
                {kitId ? (
                  <EditKitForm
                    kitToEdit={kitId}
                    hideKit={this.editKit}
                    updateKit={updateKit}
                    sendCalibrationNotification={sendCalibrationNotification}
                    kits={kits}
                    error={error}
                  />
                ) : (
                  <KitForm hardwaresItems={hardwares} {...this.props} />
                )}
              </div>
            </Col>
          )}
          <Col lg="7">
            {inventoryData && (
              <InventoryIssues
                inventory={inventoryData}
                addViewIssue={this.addViewIssue}
                category={issueCategory.find(item => item.name === "Kit")}
              />
            )}
          </Col>
        </Row>
      </Dashboard>
    )
  }
}

Kit.propTypes = {
  isModalOpen: PropTypes.number.isRequired,
  loading: PropTypes.bool.isRequired,
  kits: PropTypes.array.isRequired,
  hardwares: PropTypes.array.isRequired,
  getKits: PropTypes.func.isRequired,
  removeKit: PropTypes.func.isRequired,
  addKit: PropTypes.func.isRequired,
  getHardwares: PropTypes.func.isRequired,
  updateKit: PropTypes.func.isRequired,
  sendCalibrationNotification: PropTypes.func.isRequired,
}

const mapStateToProps = state => ({
  kits: selectKits(state),
  hardwares: selectHardwares(state),
  loading: loadingHardwares(state),
  error: kitErrors(state),
  roleName: selectRoleAuthorization(state),
})

export default connect(mapStateToProps, {
  getKits,
  removeKit,
  addKit,
  getHardwares,
  updateKit,
  sendCalibrationNotification,
})(Kit)
